<script>
import {getDisplayTime} from "../services/displayTime.js"
import { createEventDispatcher } from 'svelte';
import Button,{Label} from '@smui/Button';

export let image;
export let hasDeleteButton;

let landscapeClass = "portrait";


const dispatch = createEventDispatcher();

function deleteLocation(){
		dispatch('delete', image);
}


function imageLoaded(e) {
   if(e.target.width > e.target.height){
       landscapeClass = "landscape";
   }
}
function addToLocalStorage(image){
    localStorage.setItem('currentImage',JSON.stringify(image));
}

</script>

<div class="imageContainer">
<a href="/images/{image.imageKey}" on:click="{addToLocalStorage(image)}">
    <div class="imageText">{ image.imageTitle }</div>
    <div class="square">
    <img src="{image.thumbnail}" class="{landscapeClass}" on:load="{imageLoaded}" alt="{image.funimage}" />
    </div>
    <div class="imageText">{ image.funFact }</div>
    <div class="imageSubtitle">{ getDisplayTime(image.insertTime) }</div>
</a>
  {#if hasDeleteButton}
        <Button on:click={deleteLocation}
        variant="raised"
         class="formButton">
        <Label>Delete location</Label>
        </Button>
    {/if}

</div>

<style>.imageContainer{border:2px solid #b7b3b3;width:100%;margin:5px;padding:15px;background-color:#ffff65;box-shadow:-3px -6px rgba(209,210,183,.18039);font-size:18px}.imageText{width:100%}.imageSubtitle{font-size:12px;float:right}.square{position:relative;width:200px;height:200px;overflow:hidden;margin:auto}img{position:absolute;max-width:100%;width:100%;height:auto;top:50%;left:50%;transform:translate(-50%,-50%)}img.landscape{height:100%;width:auto;-o-object-fit:cover;object-fit:cover}
/*# sourceMappingURL=src/components/Image.svelte.map */</style>